import React from 'react'
import { Text } from '@botonic/react'
import fetch from 'isomorphic-fetch'

export default class extends React.Component {
    constructor(props) {
        super(props)
        this.state = { data: "..." }
    }

    static async botonicInit(request) {
        const question = request.input.data
        return { question }
    }



    async componentDidMount() {
        console.log(this.props.question)
        const request = {
            method: "POST",
            headers: {
                "Content-Type": "application/json"
            },
            body: JSON.stringify({ question: this.props.question })
        }

        // // Simple POST request with a JSON body using fetch
        try {
            var response = await fetch('http://localhost:8080/chat/askquestion/', request)
            var answer = await response.text()
            this.setState({data: answer})
        } catch (error) {
            console.log(error.message)

            this.setState({ data: "Request Failed." })
        }
    }

    render() {
        const c = this.state.data

        return (
            <>
                <Text>{c}</Text>
            </>
        )
    }
}